import Vue from 'vue'
import Router from 'vue-router'
import HelloWord from '@/components/HelloWord'
import Hello from '@/components/Hello'
import User from '@/components/User'
import List from '@/components/List'
import Home from '@/components/Home'
import From from '@/components/From'
import UserFrom from '@/components/UserFrom'
import TitleNav from '@/components/TitleNav'
import Login from '@/components/Login'
import ZhihuView from '@/views/ZhihuView'
import Account from '@/views/Account'
import Tableone from '@/views/Tableone'
import Tabletwo from '@/views/Tabletwo'
import Tablethree from '@/views/Tablethree'
import Tablefor from '@/views/Tablefor'
import Tablefan from '@/views/Tablefan'

Vue.use(Router)

export default new Router({
  mode: 'history',
  scrollBehavior: () => ({y: 0}),
  routes: [
    {
      path: '/',
      name: 'HelloWord',
      redirect: '/UserFrom',
      component: HelloWord,
      children: [
        {path: 'hello', name: 'Hello', component: Hello},
        {path: 'User', name: 'User', component: User},
        {path: 'List', name: 'List', component: List},
        {path: 'Home', name: 'Home', component: Home},
        {path: 'From', name: 'From', component: From},
        {path: 'UserFrom', name: 'UserFrom', component: UserFrom},
        {path: 'TitleNav', name: 'TitleNav', component: TitleNav},
        {path: 'ZhihuView', name: 'ZhihuView', component: ZhihuView},
        {path: 'Account', name: 'Account', component: Account},
        {path: 'Tableone', name: 'TableOne', component: Tableone},
        {path: 'Tabletwo', name: 'Tabletwo', component: Tabletwo},
        {path: 'Tablethree', name: 'Tablethree', component: Tablethree},
        {path: 'Tablefor', name: 'Tablefor', component: Tablefor},
        {path: 'Tablefan', name: 'Tablefan', component: Tablefan}
      ]
    },
    {path: '/login', name: 'Login', component: Login},
    {path: '/', name: 'Login', component: Login}
  ]
})



