<template>
    <div class="account">
        <div class="account-fm">
            <el-form :inline="true" :model="formInlin">
                <el-input placeholder="请输入内容" v-model="formInlin.name" class="input-with-select" @input="onSearch">
                    <el-select v-model="select" slot="prepend" placeholder="请选择">
                    <el-option label="餐厅名" value="1"></el-option>
                    <el-option label="订单号" value="2"></el-option>
                    <el-option label="用户电话" value="3"></el-option>
                    </el-select>
                    <el-button slot="append" icon="el-icon-search"></el-button>
                </el-input>
            </el-form>
            <div class="butCreate">
                <el-button type="success" class="create-btn" @click="showAdd"><i class="el-icon-plus"></i>新建子账号</el-button>
                <el-button type="success" class="create-more" @click="editVisibletwo = true"><i class="el-icon-document"></i>批量创建</el-button>
            </div>   
        </div>
        <el-table class="tableData"
            :data="tableData"
            border
            style="width: 100%">
            <el-table-column
            fixed
            prop="tel"
            label="子账号"
            width="150"
            align="center">
            </el-table-column>
            <el-table-column
            prop="name"
            label="姓名"
            width="150"
            align="center">
            </el-table-column>
            <el-table-column
            prop="province"
            label="角色"
            width="150"
            align="center">
            </el-table-column>
            <el-table-column
            prop="date"
            label="创建时间"
            align="center">
            </el-table-column>
            <el-table-column
            fixed="right"
            label="操作"
            width="150"
            align="center">
            <template slot-scope="scope">
                <el-button type="text" size="small" class="del-text-btn" disabled>禁用</el-button>
                <el-button type="text" size="small" @click="editUser(scope.row)">编辑</el-button>
            </template>
            </el-table-column>
        </el-table>

        <el-dialog :title = 'editorUsers === "add" ? "创建用户" : "编辑用户"' :visible.sync = "editVisible">
            <el-form :model = "editorUser">
                <el-form-item label="姓名">
                    <el-input v-model ="editorUser.name" class="el-input"></el-input>
                </el-form-item>
                <el-form-item label="电话">
                    <el-input v-model ="editorUser.tel" class="el-input"></el-input>
                </el-form-item>
                <el-form-item label="角色">
                    <el-input v-model ="editorUser.province" class="el-input"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer">
                <el-button @click="editVisible= false">取消</el-button>
                <el-button type="primary" @click="confirmEdit">确定</el-button>
            </div>
        </el-dialog>

        <el-dialog :visible.sync = "editVisibletwo">
         <template>
           <el-form :model = "batchUser" class="batchUser">
             <el-form-item class="batchUsers">
               <el-button type="primary" class="returnBtn">返回</el-button>
               <h3>批量创建子账号</h3>
             </el-form-item>     
           <div>
            <div>
              <span class="batchTile pr20p">上传用户资料表格</span>
              <span>请先下载模版表格，填写您需要开通的用户账号（手机号），姓名，以及角色信息，再将已完成的表格上传</span>
           </div>
           <div class="excal pt40p pb40p">
             <el-button>上次Excal</el-button>
             <span>Excal模版下载</span>
           </div>
           <div class="pasBtn mt20p">
            <span class="batchTile pr20p">设定统一密码</span>
            <span>请设定批量开通的子账号的统一密码</span>
           </div>
           <div class="inputPas">
             <el-form-item label="密码">
                <el-input type="text" v-model ="password" placeholder="请输入6-20位字符"></el-input>
              </el-form-item>
              <el-form-item label="确认密码">
                <el-input type="text" v-model ="newpassword" placeholder="请输入6-20位字符"></el-input>
              </el-form-item>
            </div>
           </div>           
           <el-button type="success">提交</el-button>
          </el-form>
         </template>
        </el-dialog>
    </div>
    
</template>

<script>
  export default {
    data () {
      return {
        formInlin: {
          name: '',
          tel: '',
          date: '',
          province: ''
        },
        editorUsers: 'add',
        editVisible: false,
        editVisibletwo: false,
        editorUser: {
          name: '',
          tel: '',
          date: '',
          province: ''
        },
        initUser: {
          uid: '',
          name: '',
          tel: '',
          data: '',
          province: ''
        },
        tableData: [],
        allTableData: [{
          tel: '13568759898',
          date: '2016-05-03',
          name: '王小虎',
          province: '会技'
        }, {
          tel: '13568759898',
          date: '2016-05-02',
          name: '潘太华',
          province: '记者'
        }, {
          tel: '13668759898',
          date: '2016-05-04',
          name: '刘花',
          province: '记者'
        }, {
          tel: '13868759898',
          date: '2016-05-01',
          name: '二小虎',
          province: '运动员'
        }]
      }
    },
    created () {
      this.tableData = this.allTableData
    },
    methods: {
      onSearch () {
        let arr = []
        this.allTableData.forEach((ele) => {
          console.log('allTableData')
          let s = this.formInlin
          let operator = true
          if (s.name) {
            operator = operator && (ele.name.indexOf(s.name) > -1)
          }
          if (operator) {
            arr.push(ele)
          }
        })
        this.tableData = arr
      },
      showAdd () {
        this.editorUser = Object.assign({}, this.initUser)
        this.editStatus = 'add'
        this.editVisible = true
      },
      editUser (row) {
        this.editorUser = Object.assign({}, row)
        this.editStatus = 'edit'
        this.editVisible = true
      },
      confirmEdit () {
        if (this.editStatus === 'add') {
          let newUser = Object.assign({}, this.editorUser)
          newUser['uid'] = this.allTableData.length + 1
          this.allTableData.push(newUser)
          this.editorUser = Object.assign({}, this.initUser)
          this.$message({
            type: 'success',
            message: '添加成功！'
          })
        } else {
          this.tableData.forEach((ele, i) => {
            if (ele.uid === this.editorUser.uid) {
              this.$set(this.tableData, i, this.editorUser)
            }
          })
          this.$message({
            type: 'success',
            message: '修改成功'
          })
        }
        this.editVisible = false
      },
      batch () {
        this.editVisibletwo = true
      }
    }
  }
</script>

<style lang="scss" scope>
.account-fm {
    width: 100%;
    display: flex;
    justify-content:space-between;
}
.tableData{
    margin-top:20px;
}

.el-form-item__content {
  display:flex;
    h3{
    margin: 0 auto;
  }
}
.batchTile{
  font-weight:bold;
  color:blue;
}
.excal{
    display: flex;
    justify-content: space-evenly;
}
.pasBtn,.inputPas{
  display:flex;
}

.inputPas{
    flex-direction: column;
    width: 50%;
}

</style>


