<template>
 <div>
   <el-form :inline = "true" :model= "UserData" ref="form" class="UserData">
     <el-form-item label="用户名："label-width="80px" ></label>
       <el-input v-model="UserData.name" placeholder="请输入用户名" @input="onSeater"></el-input>
     </el-form-item>
     <el-form-item>
       <el-button class="mr20" @click="onSeater">搜索</el-button>
     </el-form-item>
   </el-form>

     <el-table
        :data="tableData2"
        style="width: 100%"
        :row-class-name="tableRowClassName">
        <el-table-column
        prop="date"
        label="日期"
        width="180"
        align="center">
        </el-table-column>
        <el-table-column
        prop="name"
        label="姓名"
        width="180"
        align="center">
        </el-table-column>
        <el-table-column
        prop="address"
        label="地址"
        align="center">
        </el-table-column>
    </el-table>
 </div>
</template>

<script>
export default {
  data () {
    return {
      UserData: {
        name: ''
      },
      tableData2: [],
      alltableData2: [{
        date: '2016-05-02',
        name: '张小虎',
        address: '北京市普陀区金沙江路 1518 弄'
      }, {
        date: '2016-05-04',
        name: '六小虎',
        address: '上海市普陀区金沙江路 1518 弄'
      }, {
        date: '2016-05-01',
        name: '二小虎',
        address: '北京市普陀区金沙江路 1518 弄'
      }, {
        date: '2016-05-03',
        name: '王小虎',
        address: '上海市普陀区金沙江路 1518 弄'
      }]
    }
  },
  created () {
    this.tableData2 = this.alltableData2
  },
  methods: {
    tableRowClassName ({row, rowIndex}) {
      if (rowIndex === 1) {
        return 'warning-row'
      } else if (rowIndex === 3) {
        return 'success-row'
      }
      return ''
    },
    onSeater () {
      let arr = []
      this.alltableData2.forEach((ele) => {
        let s = this.UserData
        if (ele.name.indexOf(s.name) > -1) {
          arr.push(ele)
        }
      })
      this.tableData2 = arr
    }
  }
}
</script>
<style lang="scss" scope>
  .UserData{
    display:flex;
  }
  .el-table .warning-row {
    background: oldlace;
  }
  .el-table .success-row {
    background: #f0f9eb;
  }
</style>


