<template>
  <div class="page-con">
    <el-table
    :data="tableData2"
    border stripe
    style="width: 100%":row-clss-name="tableRowClassName">
    <el-table-column 
      prop="date"
      label="日期"
      align = "center"
      width="150">
    </el-table-column>
    <el-table-column
      prop="name"
      label="姓名"
      align="center"
      width="120">
    </el-table-column>
    <el-table-column
      prop="province"
      label="省份"
      align="center"
      width="120">
    </el-table-column>
    <el-table-column
      prop="city"
      label="市区"
      align="center"
      width="120">
    </el-table-column>
    <el-table-column
      prop="address"
      label="地址"
      align="center"
      width="300">
    </el-table-column>
    <el-table-column
      prop="zip"
      label="邮编"
      align="center"
      width="120">
    </el-table-column>
    <el-table-column
      fixed="right"
      label="操作"
      align="center"
      width="100">
      <template slot-scope="scope">
        <el-button v-popover:popover4 type="text" size="small">查看</el-button>
        <el-button type="text" size="small">编辑</el-button>
      </template>
    </el-table-column>
  </el-table> 
  <div>
    <el-popover
