<template>
  <div>
      <el-form :inline ="true" :model="UserData" ref="form" class="UserData">
          <el-form-item label="用户名">
              <el-input placeholder="请输入用户名" v-model="UserData.name" @input="onSeach"></el-input>
          </el-form-item>
          <el-form-item label="地址">
              <el-input placeholder="请输入地址" v-model="UserData.address" @input="onSeach"></el-input>
          </el-form-item>
          <el-form-item>
              <el-button @click="onSeach">搜索</el-button>
          </el-form-item>
      </el-form>
      <el-table
    :data="tableData"
    border
    style="width: 100%">
    <el-table-column
      prop="date"
      label="日期"
      width="180"
      align="center">
    </el-table-column>
    <el-table-column
      prop="name"
      label="姓名"
      width="180"
      align="center">
    </el-table-column>
    <el-table-column
      prop="address"
      label="地址"
      align="center">
    </el-table-column>
  </el-table>
  </div>
</template>

<script>
export default {
  data () {
    return {
      UserData: {
        name: '',
        address: ''
      },
      tableData: [],
      alltableData: [{
        date: '2016-05-03',
        name: '王小虎',
        province: '上海',
        city: '普陀区',
        address: '上海市普陀区金沙江路 1518 弄',
        zip: 200333,
        tag: '家'
      }, {
        date: '2016-05-02',
        name: '张小虎',
        province: '上海',
        city: '普陀区',
        address: '北京市普陀区金沙江路 1518 弄',
        zip: 200333,
        tag: '公司'
      }, {
        date: '2016-05-04',
        name: '二小虎',
        province: '上海',
        city: '普陀区',
        address: '上海市普陀区金沙江路 1518 弄',
        zip: 200333,
        tag: '家'
      }, {
        date: '2016-05-01',
        name: '王小虎',
        province: '上海',
        city: '普陀区',
        address: '天津市普陀区金沙江路 1518 弄',
        zip: 200333,
        tag: '公司'
      }]
    }
  },
  created () {
    this.tableData = this.alltableData
  },
  methods: {
    onSeach () {
      let arr = []
      this.alltableData.forEach((ele) => {
        let s = this.UserData
        if ((ele.name.indexOf(s.name) > -1) && (ele.address.indexOf(s.address) > -1)) {
          arr.push(ele)
        }
      })
      this.tableData = arr
    }
  }
}
</script>

<style lang="scss" scope>
.UserData{
    display:flex;
}
</style>


