<template>
 <div>
     <el-form :inline="true" :model="InputData" class="InputData">
         <el-form-item label="用户名">
             <el-input v-model="InputData.name" placeholder="请输入用户名" @input="onSeach"></el-input>
         </el-form-item>
         <el-form-item label="电话">
             <el-input v-model="InputData.tel" placeholder="请输入电话" @input="onSeach"></el-input>
         </el-form-item>
         <el-form-item label="地址">
             <el-input v-model="InputData.address" placeholder="请输入地址" @input="onSeach"></el-input>
         </el-form-item>
         <el-form-item label="日期">
             <el-input v-model="InputData.date" placeholder="请输入日期" @input="onSeach"></el-input>
         </el-form-item>
         <el-form-item>
             <el-button @click="onSeach">搜索</el-button>
         </el-form-item>
     </el-form>

      <el-table
    :data="tableData2"
    style="width: 100%"
    :row-class-name="tableRowClassName">
    <el-table-column
      prop="date"
      label="日期"
      width="180">
    </el-table-column>
    <el-table-column
      prop="name"
      label="姓名"
      width="180">
    </el-table-column>
    <el-table-column
      prop="tel"
      label="电话"
      width="180">
    </el-table-column>
    <el-table-column
      prop="address"
      label="地址">
    </el-table-column>
  </el-table>
 </div>
</template>

<script>
export default {
  data () {
    return {
      InputData: {
        name: '',
        tel: '',
        address: '',
        date: ''
      },
      tableData2: [],
      alltableData2: [{
        date: '2016-05-02',
        tel: '13546376767',
        name: '王二虎',
        address: '天津市普陀区金沙江路 1518 弄'
      }, {
        date: '2015-05-04',
        name: '李小虎',
        tel: '13546376767',
        address: '北京市普陀区金沙江路 1518 弄'
      }, {
        date: '2014-05-01',
        name: '张小虎',
        tel: '13946376767',
        address: '上海市普陀区金沙江路 1518 弄'
      }, {
        date: '2016-05-03',
        name: '王小虎',
        tel: '13546376767',
        address: '上海市普陀区金沙江路 1518 弄'
      }]
    }
  },
  created () {
    this.tableData2 = this.alltableData2
  },
  methods: {
    tableRowClassName ({row, rowIndex}) {
      if (rowIndex === 1) {
        return 'warning-row'
      } else if (rowIndex === 3) {
        return 'success-row'
      }
      return ''
    },
    onSeach () {
      let arr = []
      this.alltableData2.forEach((ele) => {
        let s = this.InputData
        let operator = true
        if (s.name) {
          operator = operator && (ele.name.indexOf(s.name) > -1)
        }
        if (s.tel) {
          operator = operator && (ele.tel.indexOf(s.tel) > -1)
        }
        if (s.address) {
          operator = operator && (ele.address.indexOf(s.address) > -1)
        }
        if (s.date) {
          operator = operator && (ele.date.indexOf(s.date) > -1)
        }
        if (operator) {
          arr.push(ele)
        }
      })
      this.tableData2 = arr
    }
  }
}
</script>


<style lang="scss" scope>
  .InputData{
      display:flex;
  }
  .el-table .warning-row {
    background: oldlace;
  }

  .el-table .success-row {
    background: #f0f9eb;
  }
</style>


