<template>
  <div class="page-con-r">
      <h3>知乎日报</h3>
      <span class="mr10p mt20p">日期：{{ nowDate }}</span>
      <div class="new-Carousel">
        <el-carousel indicator-position="outside">
            <el-carousel-item v-for="(news ,id) in imageList" :key="id">
               <img :src="news['image']">
            </el-carousel-item>
        </el-carousel>
      </div>

      <div class="news-con">
          <div v-for="(news, id) in newsList" :key="id" class="new-item">
                <el-card :body-style="{ padding: '0px' }">
                    <img :src="news['images'][0]" class="image">
                    <div class="p20p">
                    <span class="new-title" :title="news.title">{{news.title}}</span>
                    <div class="bottom clearfix">
                        <span class="time fl">{{ nowDate }}</span>
                        <el-button type="text" class="button fr" @click="showDetailsDia(news.id)">查看详情</el-button>
                    </div>
                    </div>
                </el-card>
          </div>
      </div>
      <el-dialog :visible.sync ="editVisible">
          <img :src="newsDetails.image" style="width:100%">
          <div v-html="newsDetails.body"></div>
          <div slot="footer" class="dialog-footer"></div>
      </el-dialog>
  </div>
</template>

<script>
import zhihuFetch from '@/utils/zhihuFetch'

export default {
  data () {
    return {
      editVisible: false,
      editStatus: 'add',
      imageList: [],
      newsList: [],
      nowDate: '',
      newsDetails: {
        body: '',
        image: ''
      }
    }
  },
  created () {
    this.getNewsList()
  },
  methods: {
    getNewsList () {
      zhihuFetch('news/latest', '').then(res => {
        console.log(res)
        let d = res.date
        d = d.substring(0, 4) + '年' + d.substring(5, 6) + '月' + d.substring(6, 8) + '日'
        this.nowDate = d

        this.newsList = res.stories
        this.imageList = res.top_stories
      }).catch(err => {
        this.$message({
          message: '请求失败' + err.message,
          type: 'warning'
        })
        console.log(err)
      })
    },
    showDetailsDia (id) {
      this.getNewsContent(id)
    },
    getNewsContent (id) {
      zhihuFetch('news/' + id, '').then(res => {
        // console.log(res)
        this.newsDetails = res
        this.editVisible = true
      }).catch(err => {
        this.$message({
          message: '请求失败' + err.message,
          type: 'warning'
        })
        console.log(err)
      })
    }
  }
}
</script>

<style lang="scss" scope>
  .new-Carousel img{
    width:100%;
    height:300px;
  }
 .el-carousel__item h3 {
    color: #475669;
    font-size: 18px;
    opacity: 0.75;
    line-height: 300px;
    margin: 0;
  }
  
  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }
  
  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }
  h3{
      width:80%;
      text-align: left;
      padding-top:10px;
      display: inline-block;
  }
  .news-con {
      text-align:center;
      .new-item {
        width: 260px;
        margin: 30px;
        display: inline-block;
        img{
            width: 260px;
            width: 100%;
            display: block;
        }
        .new-title{
                height: 38px;
                font-size: 14px;
                text-align: left;
                display: -webkit-box;
                -webkit-line-clamp: 2;
                -webkit-box-orient: vertical;
                overflow: hidden;  
        }
        .time{
            height: 38px;
            line-height: 38px;
        }
        .botton{
            font-size:14px;
        }   
    }
    
  }
  
</style>



