<template>
 <div>
     <el-form :inline = "true" :model="InputUser" ref="form" class="InputUser">
         <el-form-item label="用户名">
             <el-input v-model="InputUser.name" placeholder="请输入用户名" @input="onSeach"></el-input>
         </el-form-item>
          <el-form-item label="电话">
             <el-input v-model="InputUser.tel" placeholder="请输入电话" @input="onSeach"></el-input>
         </el-form-item>
          <el-form-item label="日期">
             <el-input v-model="InputUser.date" placeholder="请输入日期" @input="onSeach"></el-input>
         </el-form-item>
          <el-form-item label="地址">
             <el-input v-model="InputUser.address" placeholder="请输入地址" @input="onSeach"></el-input>
         </el-form-item>
          <el-form-item label="省份">
             <el-input v-model="InputUser.province" placeholder="请输入省份" @input="onSeach"></el-input>
         </el-form-item>
         <el-form-item>
             <el-button @click="onSeach">搜索</el-button>
         </el-form-item>
     </el-form>
    <el-table
    :data="tableData"
    border
    style="width: 100%">
    <el-table-column
      fixed
      prop="date"
      label="日期"
      align="center"
      width="150">
    </el-table-column>
    <el-table-column
      prop="name"
      label="姓名"
      align="center"
      width="150">
    </el-table-column>
    <el-table-column
      prop="province"
      label="省份"
      align="center"
      width="150">
    </el-table-column>
    <el-table-column
      prop="tel"
      label="电话"
      align="center"
      width="120">
    </el-table-column>
    <el-table-column
      prop="address"
      align="center"
      label="地址">
    </el-table-column>
    <el-table-column
      fixed="right"
      label="操作"
      align="center"
      width="100">
      <template slot-scope="scope">
        <el-button @click="handleClick(scope.row)" type="text" size="small">查看</el-button>
        <el-button type="text" size="small">编辑</el-button>
      </template>
    </el-table-column>
  </el-table>
 </div>
</template>

<script>
export default {
  data () {
    return {
      InputUser: {
        name: '',
        tel: '',
        date: '',
        address: '',
        province: ''
      },
      tableData: [],
      alltableData: [{
        date: '2014-05-03',
        name: '张小虎',
        province: '上海',
        tel: '13934323434',
        address: '上海市普陀区金沙江路 1518 弄',
        zip: 200333
      }, {
        date: '2015-05-02',
        name: '李小虎',
        province: '上海',
        tel: '13534323434',
        address: '上海市普陀区金沙江路 1518 弄',
        zip: 200333
      }, {
        date: '2016-05-04',
        name: '王二虎',
        province: '北京',
        tel: '13834323434',
        address: '北京市普陀区金沙江路 1518 弄',
        zip: 200333
      }, {
        date: '2016-05-01',
        name: '王小虎',
        province: '北京',
        tel: '13834323434',
        address: '北京市普陀区金沙江路 1518 弄',
        zip: 200333
      }]
    }
  },
  created () {
    this.tableData = this.alltableData
  },
  methods: {
    handleClick (row) {
    },
    onSeach () {
      let arr = []
      this.alltableData.forEach((ele) => {
        let s = this.InputUser
        let operator = 'true'
        if (s.name) {
          operator = operator && (ele.name.indexOf(s.name) > -1)
        }
        if (s.tel) {
          operator = operator && (ele.tel.indexOf(s.tel) > -1)
        }
        if (s.date) {
          operator = operator && (ele.date.indexOf(s.date) > -1)
        }
        if (s.address) {
          operator = operator && (ele.address.indexOf(s.address) > -1)
        }
        if (s.province) {
          operator = operator && (ele.address.indexOf(s.province) > -1)
        }
        if (operator) {
          arr.push(ele)
        }
      })
      this.tableData = arr
    }
  }
}
</script>

<style lang="scss" scope>
.InputUser{
    display:flex;
}
</style>


