<template>
  <div class="page-From">
    <h3>用户列表</h3>
    <el-form :inline="true" :model="formInline" class="demo-form-inline">
      <el-form-item label="用户名">
        <el-input v-model="formInline.name" placeholder="请输入用户名" @input="onSearch"></el-input>
      </el-form-item>
      <el-form-item label="联系方式">
        <el-input v-model="formInline.tel" placeholder="请输入联系方式" @input="onSearch"></el-input>
      </el-form-item>
      <el-form-item label="地址">
        <el-input v-model="formInline.address" placeholder="请输入地址" @input="onSearch"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click = "onSearch">查询</el-button>
      </el-form-item>
      <el-button type="success" @click = "showAddDia" class="btnPush"><i class="el-icon-plus">新建用户</i></el-button>
    </el-form>

    <el-table :data="tableData" border stripe style="width: 100%">
      <el-table-column type="index" width="180"></el-table-column>
      <el-table-column prop="date" label="日期" width="180"></el-table-column>
      <el-table-column prop="name" label="用户名" width="180"></el-table-column>
      <el-table-column prop="tel" label="手机" width="180"></el-table-column>
      <el-table-column prop="address" label="地址"></el-table-column>
      <el-table-column fixed="right" label="操作" width="180">
          <template slot-scope="scope">
              <el-button type="text" @click="editUser(scope.row)" size="small">编辑</el-button>
              <el-button type="text" @click="deleConfirm(scope.row)" size="small" class="del-text-btn">删除</el-button>
          </template>
      </el-table-column>
    </el-table>

    <el-dialog :title = 'editStatus === "add" ? "添加用户" : "修改用户"' :visible.sync = "editVisible">
      <el-form :model="editorUser" class="editor-form" label-position="top">
        <h4>基本信息</h4>
        <el-form-item label="用户名">
          <el-input v-model="editorUser.name" size="small" class="el-input"></el-input>
        </el-form-item>
        <el-form-item label="手机号">
          <el-input v-model="editorUser.tel" size="small" class="el-input"></el-input>
        </el-form-item>
        <el-form-item label="地址">
          <el-input v-model="editorUser.address" size="small" class="el-input"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dislog-footer">
        <el-button @click="editVisible = false">取消</el-button>
        <el-button type="primary" @click="confirmEdit">确定</el-button>
      </div>
    </el-dialog>
   </div>
</template>

<script>
  export default {
    data () {
      return {
        formInline: {
          name: '',
          tel: '',
          address: ''
        },
        editStatus: 'add',
        editVisible: false,
        editorUser: {
          uid: '',
          date: '',
          name: '',
          tel: '',
          address: ''
        },
        initUser: {
          uid: '',
          name: '',
          date: '',
          tel: '',
          address: ''
        },
        tableData: [],
        allTableData: [{
          uid: 1,
          date: '2016-05-03',
          name: '王小虎',
          address: '天津市普陀区金沙江路 1518 弄',
          tel: '13566889897'
        }, {
          uid: 2,
          date: '2016-05-03',
          name: '王大虎',
          address: '上海市普陀区金沙江路 1518 弄',
          tel: '13866889897'
        }, {
          uid: 3,
          date: '2016-05-03',
          name: '王老虎',
          address: '北京市普陀区金沙江路 1518 弄',
          tel: '13966889897'
        }, {
          uid: 4,
          date: '2016-05-03',
          name: '王二虎',
          address: '北京市普陀区金沙江路 1518 弄',
          tel: '17166889897'
        }]
      }
    },
    created () {
      this.tableData = this.allTableData
    },
    methods: {
      onSearch () {
        let arr = []
        this.allTableData.forEach((ele) => {
          let s = this.formInline
          let operator = true
          if (s.name) {
            operator = operator && (ele.name.indexOf(s.name) > -1)
          }
          if (s.tel) {
            operator = operator && (ele.tel.indexOf(s.tel) > -1)
          }
          if (s.address) {
            operator = operator && (ele.address.indexOf(s.address) > -1)
          }
          if (operator) {
            arr.push(ele)
          }
        })
        this.tableData = arr
      },
      showAddDia () {
        this.editorUser = Object.assign({}, this.initUser)
        this.editStatus = 'add'
        this.editVisible = true
      },
      deleConfirm (row) {
        console.log(row)
        this.$confirm('删除' + row.name + ', 是否继续?', '提示', {
          confirmButtonText: '确定',
          concelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          let newArr = []
          console.log(this.tableData)
          this.tableData.forEach(temp => {
            if (temp.uid !== row.uid) {
              newArr.push(temp)
            }
          })
          this.tableData = newArr
          this.$message({
            type: 'success',
            message: '删除成功'
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已经取消删除'
          })
        })
      },
      editUser (row) {
        this.editorUser = Object.assign({}, row)
        this.editStatus = 'edit'
        this.editVisible = true
      },
      confirmEdit () {
        if (this.editStatus === 'add') {
          let newUser = Object.assign({}, this.editorUser)
          newUser['uid'] = this.allTableData.length + 1
          this.allTableData.push(newUser)
          this.editorUser = Object.assign({}, this.initUser)
          this.$message({
            type: 'success',
            message: '添加成功'
          })
        } else {
          this.tableData.forEach((ele, i) => {
            if (ele.uid === this.editorUser.uid) {
            // this.tableData[i] = this.editorUser
            // 直接查找数组元素并替换，发现列表里的值不会更新
              this.$set(this.tableData, i, this.editorUser)
            }
          })
          this.$message({
            type: 'success',
            message: '修改成功'
          })
        }
        this.editVisible = false
      }
    }
  }
</script>

<style lang="scss" scope>
  .page-From{
        box-sizing: border-box;
        width: 100%;
        margin-left: 0;
        text-align: left;
         h3{
            height: 50px;
            line-height: 50px;
            border-bottom: 1px solid #ccc;
        }
        form{
          padding:20px;
        }
        .btnPush{
          float:right;
          margin-right:20px;
        }
        .editor-form .el-input{
          max-width:664px;
        }
        .el-form-item{
          margin-bottom: 0px;
        }
        h4{
          border-bottom:1px solid #ccc;
        }
  }
 
</style>


