<template>
  <div class="user-from">
    <h3>数据信息</h3>
    <div class="block databox">
      <el-date-picker
        v-model="value6"
        type="daterange"
        range-separator="至"
        start-placeholder="开始日期"
        end-placeholder="结束日期">
      </el-date-picker>
    </div>
    <div class="Userbox">
      <el-form :inline="true" :model="formInline">
        <el-form-item label="用户名">  
            <el-input v-model="formInline.name" placeholder="请输入用户名" @input="onSearch"></el-input>
        </el-form-item>
        <el-form-item label="联系方式">
            <el-input v-model="formInline.tel" placeholder="请输入联系方式" @input="onSearch"></el-input>
        </el-form-item>
        <el-form-item>
            <el-button type="primary">查询</el-button>
        </el-form-item>
        <el-button type="success" class="pushBtn" @click="showAddDia"><i class="el-icon-plus"></i>新建</el-button>
      </el-form>
    </div>  
   
    <el-table class="tableData"
      :data="tableData"
      border
      style="width: 100%">
      <el-table-column
        fixed
        prop="date"
        label="日期"
        width="150">
      </el-table-column>
      <el-table-column
        prop="name"
        label="姓名"
        width="150"
      >
      </el-table-column>
      <el-table-column
        prop="province"
        label="省份"
        width="120">
      </el-table-column>
      <el-table-column
        prop="city"
        label="市区"
        width="120">
      </el-table-column>
      <el-table-column
        prop="address"
        label="地址">
      </el-table-column>
      <el-table-column
        prop="tel"
        label="电话"
        width="120">
      </el-table-column>
      <el-table-column
        fixed="right"
        label="操作"
        width="100">
        <template slot-scope="scope">
          <el-button @click="editUser(scope.row)" type="text" size="small">编辑</el-button>
          <el-button type="text" size="small" @click="deleConfirm(scope.row)" class="del-text-btn">删除</el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog :title = 'editStatus === "add" ? "添加用户" :"修改用户"' :visible.sync = "editVisible">
      <el-form :model = "editorUser" class="editor=from" label-position = "top">
        <h4>基本信息</h4>
        <el-form-item label="用户名">
          <el-input v-model ="editorUser.name" size="small" class="el-input"></el-input>
        </el-form-item>
        <el-form-item label="电话">
          <el-input v-model ="editorUser.tel" size="small" class="el-input"></el-input>
        </el-form-item>
        <el-form-item label="地址">
          <el-input v-model ="editorUser.address" size="small" class="el-input"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="editVisible = false">取消</el-button>
        <el-button type="primary" @click="confirmEdit">确定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
 export default {
   data () {
     return {
       value6: '',
       value7: '',
       formInline: {
         name: '',
         tel: '',
         address: ''
       },
       editStatus: 'add',
       editVisible: false,
       editorUser: {
         date: '',
         name: '',
         tel: '',
         address: ''
       },
       initUser: {
         uid: '',
         name: '',
         date: '',
         tel: '',
         address: ''
       },
       pickerOptions2: {
         shortcuts: [{
           text: '最近一周',
           onClick (picker) {
             const end = new Date()
             const start = new Date()
             start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
             picker.$emit('pick', [start, end])
           }
         }, {
           text: '最近一个月',
           onClick (picker) {
             const end = new Date()
             const start = new Date()
             start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
             picker.$emit('pick', [start, end])
           }
         }, {
           text: '最近三个月',
           onClick (picker) {
             const end = new Date()
             const start = new Date()
             start.setTime(start.getTime() - 3600 * 1000 * 24 * 90)
             picker.$emit('pick', [start, end])
           }
         }]
       },
       tableDatat: [],
       allTableData: [{
         date: '2016-05-03',
         name: '王小虎',
         province: '上海',
         city: '普陀区',
         address: '上海市普陀区金沙江路 1518 弄',
         tel: '13578767876'
       }, {
         date: '2016-05-02',
         name: '王二虎',
         province: '上海',
         city: '普陀区',
         address: '天津市普陀区金沙江路 1518 弄',
         tel: '13987876545'
       }, {
         date: '2016-05-04',
         name: '张小虎',
         province: '上海',
         city: '普陀区',
         address: '上海市普陀区金沙江路 1518 弄',
         tel: '13675645647'
       }, {
         date: '2016-05-01',
         name: '李小虎',
         province: '上海',
         city: '普陀区',
         address: '北京市普陀区金沙江路 1518 弄',
         tel: '13898909098'
       }]
     }
   },
   created () {
     this.tableData = this.allTableData
   },
   methods: {
     onSearch () {
       let arr = []
       this.allTableData.forEach((ele) => {
        //  取到所有输入框里的值
         let s = this.formInline
        //  console.log(s)
        //  ele.name
        // if ((ele.name.indexOf(s.name) > -1)){
        //   arr.push(ele)
        // }
        // if((ele.tel.indexOf(s.tel) > -1)){
        //   arr.push(ele)
        // }
        // if((ele.tel.indexOf(s.tel) > -1)){
        //   arr.push(ele)
        // }
         if ((ele.name.indexOf(s.name) > -1) && (ele.tel.indexOf(s.tel) > -1) && (ele.address.indexOf(s.address) > -1)) {
           arr.push(ele)
         }

        //  let operator = true
        //  if (s.name) {
        //    operator = operator && (ele.name.indexOf(s.name) > -1)
        //  }
        //  if (s.tel) {
        //    operator = operator && (ele.tel.indexOf(s.tel) > -1)
        //  }
        //  if (s.address) {
        //    operator = operator && (ele.address.indexOf(s.address) > -1)
        //  }
        //  if (operator) {
        //    arr.push(ele)
        //  }
       })
       this.tableData = arr
     },
     showAddDia () {
       this.editorUser = Object.assign({}, this.initUser)
       this.editStatus = 'add'
       this.editVisible = true
     },
     deleConfirm (row) {
       this.$confirm('删除' + row.name + ',是否继续?', '提示', {
         confirmButtonText: '确定',
         cancelButtonText: '取消',
         type: 'warning'
       }).then(() => {
         let newArr = []
         this.tableData.forEach(temp => {
           if (temp.uid !== row.uid) {
             newArr.push(temp)
           }
         })
         this.tableData = newArr
         this.$message({
           type: 'success',
           message: '删除成功'
         })
       }).catch(() => {
         this.$message({
           type: 'info',
           message: '已经取消删除'
         })
       })
     },
     editUser (row) {
       this.editorUser = Object.assign({}, row)
       this.editStatus = 'edit'
       this.editVisible = true
     },
     confirmEdit () {
       if (this.editStatus === 'add') {
         let newUser = Object.assign({}, this.editorUser)
         newUser['uid'] = this.allTableData.length + 1
         this.allTableData.push(newUser)
         this.editorUser = Object.assign({}, this.initUser)
         this.$message({
           type: 'success',
           message: '添加成功'
         })
       } else {
         this.tableData.forEach((ele, i) => {
           if (ele.uid === this.editorUser.uid) {
             this.$set(this.tableData, i, this.editorUser)
           }
         })
         this.$message({
           type: 'success',
           message: '修改成功'
         })
       }
       this.editVisible = false
     }
   }
 }
</script>

<style lang="scss" scope>
  .user-from{
      margin-left:0;
      text-align:left;
      box-sizing:border-box;
       h3{
        width:100%;
        border-bottom: 1px solid #ccc;
        display: block;
      }
      .databox{
        margin-top:15px;
        display:inline-block;
      }
      .Userbox{
       display: inline-block;
       position: absolute;
       margin-top: 15px;
       margin-left: 10px;
       .pushBtn{
         margin-left:100px;
       }
      }
      .tableData{
        margin-top:20px;
      }
      h4{
        border-bottom: 1px solid #ccc;
      }
    }
</style>



