<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
import Cookie from 'js-cookie'
import router from '@/router'

export default {
  name: 'app',
  created () {
    if (!Cookie.get('token')) {
      router.push('/login')
    } else {
      router.push('/')
    }
  }
}
</script>

<style>
*{
  margin:0;
  padding:0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  height:100%;
}
</style>



