<template>
  <header class="header">
   <div class="logo">Admin</div>
       <ul class="nav">
           <li>
               <a href="/">首页</a>
           </li>
       </ul>
       <div @click="logout" class="logout">退出</div>
  </header>
</template>

<script>
import Cookie from 'js-cookie'
export default {
  name: 'Header',
  methods: {
    logout () {
      let keys = Cookie.get()
      for (let k in keys) {
        Cookie.remove(k)
      }
      location.reload()
    }
  }
}
</script>

<style lang="scss" scoped>
  .header{
      color: #fff;
      height: 48px;
      line-height: 48px;
      display: flex;
      flex-direction: row;
      background-color: #37474f;
      .logo{
          width: 260px;
          font-size: 16px;
          letter-spacing: 1px;
          line-height: 48px;
      }
      .nav{
            flex: 1;
            margin: 0;
            padding: 0;
            list-style: none;
            li{
                width:120px;
                text-align: center;
            a{
                height:48px;
                display: block;
                font-size:16px;
                line-height:48px;
                color:#fff;
            }
         }
         .left{
             float:left;
             a:hover{
                 color:#eee;
                 background-color:#364428;
             }
             a.router-link-active{
                 transition: all .2s;
                 background-color:#263238;
                 border-bottom: 4px solid #26a69a;
             }
         } 
         .right{
             float: right;
             margin-right:30px;
             a{
                 position: relative;
                 padding-left:20px;
                 font-size: 14px;
                 &::before{
                     content: '';
                     position: absolute;
                     top:15px;
                     left:15px;
                     width:20px;
                     height:20px;
                     background-size:cover;
                 }
                 &:hover{
                     color:#eee;
                     background-color:#364238;
                 }
                 &.router-link-active {
                     transition: all .2s;
                     background-color:#263238;
                     border-bottom:4px solid #26a69a;
                 }
             }
         }      
      }
      .logout{
         cursor: pointer;
         font-size:14px;
         line-height:48px;
         padding-right:20px;
     }
  }
</style>



