<template>
    <div>
        <el-form :inline="true" :model="InputData" class="InputData">
            <el-form-item label="用户名">
                <el-input placeholder="请输入用户名" v-model="InputData.name" @input="onSeach"></el-input>
            </el-form-item>
            <el-form-item label="电话">
                <el-input placeholder="请输入电话" v-model="InputData.tel" @input="onSeach"></el-input>
            </el-form-item>
            <el-form-item label="地址">
                <el-input placeholder="请输入地址" v-model="InputData.address" @input="onSeach"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button @click="onSeach">搜索</el-button>
            </el-form-item>
        </el-form>

        <el-table
    :data="tableData2"
    style="width: 100%"
    :row-class-name="tableRowClassName">
    <el-table-column
      prop="tel"
      label='电话'
      align="center"
      width="180">
    </el-table-column>
    <el-table-column
      prop="name"
      label="姓名"
      align="center"
      width="180">
    </el-table-column>
    <el-table-column
      prop="address"
      align="center"
      label="地址">
    </el-table-column>
  </el-table>
    </div>
</template>

<script>
export default {
  data () {
    return {
      InputData: {
        name: '',
        tel: '',
        address: ''
      },
      tableData2: [],
      alltableData2: [{
        tel: '13546787678',
        name: '张小虎',
        address: '上海市普陀区金沙江路 1518 弄'
      }, {
        tel: '13546787678',
        name: '王小虎',
        address: '天津市普陀区金沙江路 1518 弄'
      }, {
        tel: '13946787678',
        name: '李小虎',
        address: '上海市普陀区金沙江路 1518 弄'
      }, {
        tel: '13846787678',
        name: '王二虎',
        address: '北京市普陀区金沙江路 1518 弄'
      }]
    }
  },
  created () {
    this.tableData2 = this.alltableData2
  },
  methods: {
    tableRowClassName ({row, rowIndex}) {
      if (rowIndex === 1) {
        return 'warning-row'
      } else if (rowIndex === 3) {
        return 'success-row'
      }
      return ''
    },
    onSeach () {
      let arr = []
      this.alltableData2.forEach((ele) => {
        let s = this.InputData
        if ((ele.name.indexOf(s.name) > -1) && (ele.tel.indexOf(s.tel) > -1) && (ele.address.indexOf(s.address) > -1)) {
          arr.push(ele)
        }
      })
      this.tableData2 = arr
    }
  }
}
</script>

<style lang="scss" scope>
 .InputData{
      display:flex;
  }
  .el-table .warning-row {
    background: oldlace;
  }

  .el-table .success-row {
    background: #f0f9eb;
  }
 
</style>


